.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #ddd;
    border-radius: 3px;
    color: black;
}
.subtotal__button{
    background: rgb(31, 121, 71);
    border:1px solid;
    margin-top:10px;
    border-radius: 40px;
    border-color: white;
    color:white;
}