.Signin{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.Signin__logo{
    width: 100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: auto;
    margin-left: auto;
}
.Signin__container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    background-color: rgb(111, 207, 135);;
    border: 1px solid lightgreen;
    color: white;
    padding: 20px;
}
.Signin__container > button{
    background: rgb(31, 121, 71);
    border:1px solid;
    margin-top:10px;
    border-color: white;
    color:white;
    border-radius: 40px;
}

