.Header{
    height:60px;
    display: flex;
    align-items: center;
    background-color: rgb(111, 207, 135);
    position: sticky;
    top:0;
    z-index: 100;
}
.header__logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 15px;
}
.header__search{
    display: flex;
    flex:1;
    align-items: center;
    border-radius: 24px;
}
.header__searchInput{
    height: 12px;
    width: 100%;
    padding: 10px;
    border:none;
}
.dropdown{
    width: 200px;
    border-radius: 50px;
    margin: 100px auto;
    position: relative;
}
.dropdown .dropdown-btn{
    padding:10px;
    background: #fff;
    box-shadow: 3px 3px 10px 6px rgba(0,0,0,0,0.06);
    font-weight: bold;
    height: 12px;
    border-right: solid #999;
    color: #333;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.dropdown .dropdown-content{
    position: absolute;
    top:110%;
    left:0;
    padding:15px;
    background: #fff;
    box-shadow: 3px 3px 10px 6px rgba(0,0,0,0,0.06);
    font-weight: bold;
    color: #333;
    width: 95%;
}
.dropdown .dropdown-content .dropdown-item{
    padding:10px;
    cursor: pointer;
    transition: all 0.2;
}
.dropdown .dropdown-content .dropdown-item:hover{
    background: #fcfcfc;
}
.header__searchIcon{
    padding:5px;
    height: 22px;
    background-color: rgb(31, 121, 71);
    color: white;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__option{
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    margin-top: 5px;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}
.header__BasketOption{
    display: flex;
    align-items: center;
    color: white;
}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.header__optionLineOne{
 font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}