.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin:10px;
    padding: 20px;
    width:90%;
    max-height: 400px;
    max-width: 25%;
    background: rgb(111, 207, 135);
    z-index: 1;
    color:white;
}
.product >img{
    max-height:200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product >button{
    background: rgb(31, 121, 71);
    border:1px solid;
    margin-top:10px;
    border-color: white;
    color:white;
    border-radius: 40px;
}
.product__rating{
    display: flex;
    justify-content: center;
}