.Payment{
    background-color: white;
}
.Payment__container > h1{
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234,237,237);
    border-bottom: 1px solid lightgray;
}
.Payment__container > h1 >a{
    text-decoration: none;
}
.Payment__section{
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgray;
}
.Payment__title{
    flex:0.2;
}
.Payment__address{
    flex:0.8;
}